.container {
    display: flex;
    justify-content: space-around; /* 将子元素平均地分布在容器的水平方向上 */
    top: 700px;
}

.follow-us, .contact-us, .account {
    margin: 0 10px; /* 左右各增加10px的外边距 */

}

/* Each section within the footer */
.follow-us {
    display: flex;
    flex-direction: column;  /* 确保子元素垂直排列 */
    align-items: center;     /* 子元素水平居中 */
    top: 700px;
}

.social-icons {
    margin: 5px;             /* 添加间距 */
}

.follow-us img {
    width: 25px;             /* 或其他你需要的尺寸 */
    height: auto;
    margin-right: 25px;      /* 如果你想要图标之间有空间 */
}
.icon-container {
    display: flex; /* 使图标水平排列 */
    justify-content: center; /* 使图标水平居中 */
}

/* Social media icons (just buttons in this example) */
.button {
    background: white;
    color: #333;
    border-radius: 50%;
    padding: 10px;
    margin: 5px;
}
.contact-us {
    display: flex;
    flex-direction: column; /* 设置子元素垂直排列 */
    align-items: center;    /* 设置子元素水平居中 */
}

.mail-icons {
    margin: 10px 0;
    width: 30px;
    height: 30px; /* 设置图标上下的间距 */
}
.footer {
    /*top: 800px;*/
    /*font-weight: 400;*/
    /*padding: 5px;*/
    /*!*position: absolute;*!*/
    /*width: 100%;*/
    /*display: flex;*/
    /*justify-content: space-around;*/
    flex-shrink: 0; /* Do not allow the footer to shrink */
    font-weight: 400;
    padding: 5px;
    width: 100%;
    display: flex;
    justify-content: space-around;
}

.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.content {
    flex: 1;
}

