.categories-main-content {
    display: grid;
    /*grid-template-columns: repeat(2, 50%);  !* 兩列，每列寬度為50% *!*/
    /*gap: 20px;  !* 格子間距 *!*/
    list-style: none;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;  /* 格子之間的間距 */
    padding: 0; /* 清除ul的內部邊距 */
    width:60%;
    flex: 1;
    margin-top: -40px;
    /*margin-left: 400px;*/
}


