/* 为书名设置特定字体 */
.book-title {
    font-family: 'Georgia', serif;
    font-size: 16px;
    line-height: 1.3; /* 通常为字体大小的1.3到1.6倍之间 */
    font-weight: 500;
}

/* 为作者名设置不同的字体 */
.author-name {
    font-family: 'Georgia', serif;
}

/* 为价格设置另一种字体 */
.price {
    font-family: 'Helvetica', sans-serif;
}

/* 为按钮上的文字设置字体 */
.add-to-cart-button {
    font-family: 'Verdana', sans-serif;
}
