.text-logo {
    font-family: var(--title-font-family);
}



@media (max-width: 1438px) {
    header {
        flex-direction: column;
        background-color: black;
    }
}
.login-text-button {
    background: none;
    color: #000000;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
    margin-left: 35px;
    align-self: center;
    margin-top: 10px;  /* Reduced from 20px to 10px to move the button up */
    position: absolute;


}



.login-text-button:hover {
    text-decoration: underline;}


/* 如果需要，您也可以调整与 "user icon" 的垂直对齐 */
.icon-button {
    align-self: center; /* 使得图标垂直居中 */
}
.app-container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 50px 6px;
    position: fixed;
    top: 0;
    font-weight: 700;
    margin-bottom: 20px;
    background-color: white;

}
.logo{
    width: auto;
    height: 100px;
    padding-left: 5px;}

.search-container {
    position: relative;
}

.search-bar {
    padding-right: 40px; /* space for the button, adjust based on your button's size */
    width: 100%;
}

.no-wrap {
    white-space: nowrap;
}
.icon-button.login-button {
    display: flex;
    align-items: flex-end;
}
.icon {
    margin: 0;
    /* 其他相關樣式 */
}

.login-text-button {
    margin: 20px;
    /* 其他相關樣式 */
}
.icon-img {
    margin: 0;
    /* 其他相關樣式 */
}
.icon-button.login-button:active .login-text-button {
    text-decoration: underline;
}

