.book-box {
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color:#D9E9EB; /* 或者您喜欢的任何颜色 */
    padding: 10px;
    box-shadow: 0 4px 4px 0 rgba(0,0,0,.25);
    width: 400px; /* 增加宽度 */
    height: 200px; /* 减少高度 */
    border-radius: 20px;
    flex: 0 0 calc(50% - 20px); /* 每行两个box */
    box-sizing: border-box;
    margin: 10px;
    /*position: relative;*/



}

