.book-image img {
    width: 88%;
    /*height: 30%; !* 或者您喜欢的任何高度 *!*/
    object-fit: cover;
    border-radius: 5px;
    padding-left: 2em;
    display: block;  /* 防止底部的空隙 */




}


.button {
    margin-top: 10px;
    padding: 5px 10px;
    cursor: pointer;
    background-color: #9B3333; /* 或者您喜欢的任何颜色 */
    color: #ffffff;
    border: none;
    border-radius: 5px;
    width: 130px;  /* 您可以根據需要調整這個值 */
    height: 40px;

}
.button:hover {
    background-color: #bd2e2e; /* 鼠标悬停时的颜色 */
}

.sidebar-button {
    font-size: 16px; /* 或者您想要的其他大小 */
    background: none; /* 去掉背景 */
    /* color: grey; 文字颜色为灰色 */
    text-decoration: none; /* 去掉下划线 */
}

.sidebar-button:hover {
    text-decoration: underline; /* 鼠标悬停时加上下划线 */
    font-size: 18px; /* 鼠标悬停时文字大小增大 */
}


.icon-right-top {
    position: absolute;  /* 使用絕對定位 */
    top: 0;  /* 設定為0，使icon貼齊書的圖片的頂部 */
    right: 0px;  /* 設定為0，使icon貼齊書的圖片的右邊 */
    background: none;
    border: none;
    padding: 0;
    z-index: 10;  /* 確保icon始終位於圖片上方 */
    cursor: pointer;
    /*width: 30px; !* For example, 30px *!*/
    height: 30px; /* For example, 30px */

}
.icon-right-top img {
    width: 30%; /* This ensures the image will fill the button */
    height: auto;
}





.book-image {
    display: inline-block;
    overflow: hidden;
    /*width: 200px;*/
    padding-right: 2em;
    position: relative;


}
