.body-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh; /* 确保占满视窗的高度 */
    margin: 0 auto; /* 水平居中 */
}
.booklist-item{
    width: 190px;
    height: 56px; /* 設定高度為視窗的100% */
    border: none;
    background-color: #F3F3F3;
    margin-right: 20px;
    padding: 10px;
}
.booklist-item:after {
    content: "";
    display: block;
    height: 1px;
    background-color: rgba(78, 78, 78, 0.35); /* 您可以根據需要更改顏色 */
    width: 100%;
    margin-top: 15px; /* 根據需要調整上邊距 */
}
.booklist-item:hover{

    background-color: #E4DDDD;
    text-decoration: none;

}



.categories-sidebar {
    width: 200px;
    height: auto; /* 設定高度為視窗的100% */
    background-color: #F3F3F3;
    padding: 10px;
    margin-top: 150px;
    box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.1);
    /*overflow-y: auto; !* 如果內容超出側邊欄的高度，則顯示滾動條 *!*/
    /*color: #000;*/
    flex: 0 0 auto;  /* 這會確保 sidebar 的寬度不會改變 */
    margin-right: 20px;
}
li{
    width:100%;
    height:100%;
}



.categories-sidebar ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.categories-sidebar li {
    padding: 8px;
    text-align: center;
    border-bottom: 1px solid #ccc;
    cursor: pointer;
    height: 70px; /* 或者您想要的其他高度 */
    display: flex;
    align-items: center; /* 垂直居中對齊 */
    justify-content: center; /* 水平居中對齊 */
}
.categories-sidebar a {
    text-decoration: none;
    color: rgb(0, 0, 0);
    display: block;
    padding: 5px 10px;
    border-radius: 4px;
}
.categories-sidebar li:last-child {
    border-bottom: none;
}


.categories-sidebar a {
    color: #000; /* 设定链接文字颜色为黑色 */
    text-decoration: none; /* 去掉下划线 */
}

.categories-sidebar a:hover {
    text-decoration: underline; /* 鼠标悬浮时显示下划线 */
}



.categories-container {
    display: flex;
}









