
.checkout-cart-table-view {
    display: flex;
    flex-direction: row;
    align-content: space-around;
    width: 1100px;
    /*position: relative;*/
    padding-top: 120px; /* 向下移动 */
    padding-left: 100px;
    padding-bottom: 20px;
    /*margin-bottom: 120px;*/
}
.checkout-cart-table {
    display: flex;
    flex-direction: column;
}
.checkout-cart-info {
    display: grid;
    grid-template-columns: repeat(2, 350px);
    row-gap: 2em;
    grid-gap: 0px;
    /*padding-top: 80px; !* 向下移动 *!*/
    /*padding-left: 400px;*/


}

.checkout-display {
    margin-top: 80px;
    width: 300px;
    height: 250px;
    border: 1px solid black;
    border-radius: 10px;
}
.checkout-text {
    margin-top: 40px;
    margin-left: 50px;
    margin-bottom:20px;
    /*height: 150px;*/
    /*text-align: right;*/
  /*//  border: 1px solid black;*/
  /*  border-radius: 10px;*/

}
.checkout-page-body {
    display: flex;
    flex-direction: column;
    border: 1px solid black;
    border-radius: 10px;
    padding: 1em;
    /*gap: 70px;*/
    /*position: absolute;*/
    padding-bottom: 2em;


}
.checkout-cart-info-ul {
    display: contents;
}
.checkout-cart-info-ul > li {
    display: contents;
}
.checkout-table-heading {
    background-color: rgba(79, 203, 242, 0.32);
    color: black;
}
.checkout-table-heading > * {
    background-color: rgba(79, 203, 242, 0.32);
    padding: 0.5em;
}
.checkout-heading-book {
    grid-column: 1 / 3;
}
.checkout-heading-price {
    grid-column: 3 / 5;
    text-align: right;
}
.checkout-heading-subtotal {
    text-align: right;
    grid-column: -2 / -1;
}
.checkout-cart-book-image {
    width: 150px;
    height: 200px;
}
.checkout-cart-book-info {
    margin-left: auto;
    margin-right: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-items: right;
    height: 150px;
}


.checkout-cart-book-item {
    margin-left: auto;
    margin-right: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;

    width: 320px;
    height: 220px;



}

.checkout-cart-info-img {
    width: 90%;
    height: 90%;
}
.checkout-row-line {
    border-bottom: 1px solid black;
}
.checkout-num-button {
    background-color: white;
}
.checkout-narrow-rect {
    width: 75px;
    height: 100px;
}

.checkout-square {
    width: 100px;
    height: 100px;
}

.checkout-wide-rect {
    width: 125px;
    height: 100px;
}

.checkout-cart-book-price {
    padding-left: 1em;
    text-align: right;
}

.checkout-cart-book-quantity {
    display:flex;

}

.checkout-cart-book-subtotal {
    padding-left: 1em;
    padding-right: 1em;
}
/* Row separators in the table */

.checkout-btn-primary {
    font-family: "Montserrat Alternates";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    height: 25px;
    width: 200px;
    background-color: #1510f5;
    color: white;
    border: 1px solid #555;
    margin-top: 20px;
    margin-right: 50px;
    border-radius: 40px;
}
.checkout-btn-secondary {
    font-family: "Montserrat Alternates";
    font-style: normal;
    height: 25px;
    width: 200px;
    font-size: 20px;
    margin-left: 30px;
    background-color: rgba(79, 203, 242, 0.32);
    color: black;
    border: 1px solid #555;
    margin-top: 10px;
    border-radius: 40px;
}

.checkout-btn-ter {
    font-family: "Montserrat Alternates";
    font-style: normal;
    height: 20px;
    width: 100px;
    font-size: 15px;
    margin-left: 20px;
    color: #1510f5;
    background-color: white;
    border: none;
    margin-top: 10px;
    border-radius: 40px;
}
/* Increment/decrement buttons */

.checkout-icon-button {
    border: none;
    cursor: pointer;
}

.checkout-inc-button {
    font-size: 1.125rem;
    color: var(--primary-color);
    margin-right: 0.25em;
}

.checkout-btn-ter:hover {
    color: var(--primary-color-dark);
}

.checkout-dec-button {
    font-size: 1.125rem;
    color: #ccc;
}
.checkout-dec-button:hover {
    color: #aaa;
}

/* Chevron buttons */

.checkout-dec-arrow-button,
.checkout-inc-arrow-button {
    font-size: 1rem;
    color: var(--primary-color);
}

.checkout-dec-arrow-button:hover,
.checkout-inc-arrow-button:hover {
    color: var(--primary-color-dark);
}

input[type="number"] {
    width: 4em;
}

.checkout-select {
    background-color: rgba(79, 203, 242, 0.32);
    color: black;
    border: 2px solid var(--primary-color-dark);
    border-radius: 3px;
}
form {
    display: flex;
    flex-direction: column;
    border: 1px solid black;
    border-radius: 10px;
    padding: 20px;
    /*height: 360px;*/
    width: 20em;
}
form > div {
    display: flex;
    justify-content: flex-start;
    margin-top: 0.5em;
}

form > div:first-child {
    margin-top: 0;
}
form > div > input,
form > div > select {
    background-color: rgba(79, 203, 242, 0.32);
    margin-left: 0.5em;
    /*position: relative;*/
}
form  .error {
    color: red;
    text-align: right;
    font-size: 0.9em;
    color: #cc0033;
    display: inline-block;
    line-height: 15px;
    margin: 5px 0 0;
    position: relative;
}
.checkoutStatusBox {
    margin: 1em;
    padding: 1em;
    text-align: center;
}
.checkout-view {
    dsiplay: flex;
    flex-direction: column;
    border: 1px solid black;
    border-radius: 10px;
}

.checkout-summary{
    margin-top: 20px;
    width: 15em;
}











